import React from 'react';
import classes from './ProfileInfo.module.css'
import Preloader from "../../common/Preloder/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";




const ProfileInfo = ({profile ,status, updateStatusThunk} ) => {

    if(!profile){
        return <Preloader />}

    return <div>
      <div className={classes.description}>
        <div >
            <img className={classes.avatar} src={ profile.photos.large === null ? 'https://i.pinimg.com/originals/26/a2/0a/26a20a99d83cf280fe907a14674c1ad6.png' : profile.photos.large }/>
        </div>
          <div className={classes.info}>
              Name
          </div>
         {profile.fullName}
          <div className={classes.info}>
              Status
          </div>
          <ProfileStatusWithHooks status={status} updateStatusThunk={updateStatusThunk}/>
          <div className={classes.info}>
              Info
          </div>
          <div >
              About me - {profile.aboutMe}
          </div>
          <div className={classes.info}>
              Contacts
          </div>
          <div>

              <div>facebook - {profile.contacts.facebook}</div>
              <div>website - {profile.contacts.website}</div>
              <div>vk - {profile.contacts.vk}</div>
              <div>twitter - {profile.contacts.twitter}</div>
              <div>instagram - {profile.contacts.instagram}</div>
              <div>youtube - {profile.contacts.youtube}</div>
              <div>github - {profile.contacts.github}</div>
              <div>mainLink - {profile.contacts.mainLink}</div>
          </div>
          <div>lookingForAJob - {profile.lookingForAJob}</div>
          <div>lookingForAJobDescription - {profile.lookingForAJobDescription}</div>

          <div>userId - {profile.userId}</div>
      </div>
    </div>
}

export default ProfileInfo;